import React from 'react'
import { Image } from 'react-bootstrap'
import { baseUrl } from '../../../../Assets/AssetsHelper'
import { ModalBody, Modal } from "react-bootstrap";
import ReactPlayer from 'react-player';

const getDetails = () => {

    const content = window?.APP_CONTENT
    const popup = content?.auto_pilot?.upgrade_popup ?? {}

    let obj = {
        heading: popup?.title ?? '',
        description: popup?.dics ?? '',
        url: baseUrl(`media/products/auto-pilot-feature.mp4`),
        btnText: popup?.btn_text ?? '',
        isVideo: true
    }
    return obj;
}
const UpgradePop = ({ show, handleClose, navigate }) => {

    const { heading, description, url, isVideo, btnText } = getDetails();

    return (
        <Modal show={show} onHide={handleClose} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
            <ModalBody className="p-8 socialwall-popup-body">
                <div className="row align-itmes-center">
                    <h2 className="font-weight-boldest w-100 text-center mt-0">{heading}</h2>
                    <p className="mb-5 text-dark text-center">{description}</p>
                    <div className="col-lg-12">
                        <div className="switch_image ugc-switch-video mb-4 w-100">
                            {!isVideo ? <Image isPrefix={true} className="img-fluid" src={url} alt='' /> :
                                <ReactPlayer height={300} id="bg_image" className="object-fit-contain" url={url} autoPlay={true} playing={true} loop={true} muted={true} controls={false} playsinline={true} />
                            }
                        </div>
                    </div>
                    <div className="col-lg-12">
                        <div className="switch_text_inner text-center">
                            <button onClick={() => { navigate('/price') }} className={`btn btn-primary btn-lg btn-min-w mt-2`}>{btnText}</button>
                        </div>
                    </div>
                </div>
            </ModalBody>
        </Modal>
    )
}

export default UpgradePop