import React, { useMemo, useState } from 'react'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { checkPermission } from '../../../../../actions/collaboratorActions';
import { useNavigate } from 'react-router-dom';
import UpgradePop from '../../CommonComponents/UpgradePop';
import { shallowEqual, useSelector } from 'react-redux';
import { PRODUCT_PLANS } from '../../../../../actions/constants';

const SideMenu = ({ getWallData }) => {
  const { name, id } = getWallData || {}
  const navigate = useNavigate()
  const path = window.location.pathname;
  const [pop, setPop] = useState(false)
  const planId = useSelector((state) => state.authorized?.userData?.user_data?.UserPlan?.planId, shallowEqual)
  const checkPlan = PRODUCT_PLANS.freePlan.includes(planId) || PRODUCT_PLANS.freeTrail.includes(planId)
  const navigateHandler = (e, path, key) => {
    e.stopPropagation()
    if (checkPermission('networkManager', 'manageFeed', true) || [4, 5].includes(key)) navigate(path);
  }

  const breadCrumbs = useMemo(() => window?.APP_CONTENT?.['sideBar'] ?? {}, []);
  return (
    <>
      <div className="custom-side-menu-inn h-100">
        <div className="custom-side-menu-body">
          <p className="csm-title mb-0 pb-3 pb-sm-2 border-bottom text-uppercase">{breadCrumbs?.content ?? ""}</p>
          <ul className="list-unstyled">
            <li id='add_feed' className={`py-3 border-bottom cursor-pointer ${path?.includes('addfeed') && !path?.split('/')?.includes('29') ? 'active' : ''}`} onClick={(e) => navigateHandler(e, `/content/addfeed/${id}`, 2)}>
              <FontAwesomeIcon iconClass="me-2" icon="square-plus" />
              <span>{breadCrumbs?.addFeed ?? ""}</span></li>
            {/* <li id='manage_content' className={`py-3 border-bottom cursor-pointer ${path?.includes('moderation') ? 'active' : ''}`} onClick={(e) => navigateHandler(e, `/content/moderation/${id}`, 1)}>
              <FontAwesomeIcon iconClass="me-2" icon="image" />
              <span>Manage Content</span></li> */}
            <li id='manage_feed' className={`py-3 border-bottom cursor-pointer ${path?.includes('managefeed') ? 'active' : ''}`} onClick={(e) => navigateHandler(e, `/content/managefeed/${id}`, 3)}>
              <FontAwesomeIcon iconClass="me-2" icon="rectangle-history-circle-plus" />
              <span>{breadCrumbs?.manageFeed ?? ""}</span></li>
            <li id='autopilot' className={`py-3 border-bottom cursor-pointer ${path?.includes('autopilot') ? 'active' : ''}`} onClick={(e) => checkPlan ? setPop(true) : navigateHandler(e, `/autopilot`, 4)}>
              <FontAwesomeIcon iconClass="me-2" icon="toggle-on" />
              <span>{breadCrumbs?.autopilot ?? ""}</span></li>
            <li id='review_hub' className={`py-3 border-bottom cursor-pointer ${path?.split('/')?.includes('29') ? 'active' : ''}`} onClick={(e) => navigateHandler(e, `/content/addfeed/${id}/29`, 5)}>
              <FontAwesomeIcon iconClass="me-2" icon="messages" />
              <span>{breadCrumbs?.reviewHub ?? ""}</span></li>
          </ul>
        </div>
        <div className="custom-side-menu-footer d-flex justify-content-end border-top">
          <div className="f-between w-100 gap-3">
            <button type="button" id='publish_btn' className="w-100 btn btn-primary" onClick={(e) => { e.stopPropagation(); navigate(`/publish/${id}`) }}>
              {breadCrumbs?.publish_ ?? ""}
            </button>
          </div>
        </div>
      </div>
      {pop ? <UpgradePop show={pop} handleClose={() => setPop(false)} navigate={navigate}/> : null}
    </>
  )
}

export default SideMenu