import { Component } from "react";
import { Button, Card, Col, Container, Image, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { generateRandomArray, GET_NETWORK_ICON, GET_NETWORK_NAME, shopifyApp } from "../../../../../utils";
import withRouter from "../../../../routes/WithRouter";
import SideMenu from "../../Moderation/SideMenu";
import { connect } from "react-redux";
import { GET_ADD_FEED } from "../../../../../actions/moderationActions";

class ManualHead extends Component{
    state={
        networkList:[],
        loader:true
    }
    
    componentDidMount(){
        const {networks, getNetworks} = this.props
        if(!networks) getNetworks()
        else this.setNetworks()
    }

    setNetworks = () => {
        const {networks} = this.props
        const tabs = networks?.find(net=> net.id == 0)
        this.setState({loader:false, networkList: tabs?.access ? tabs?.networkTab : []})
    }

    componentDidUpdate(prevProps){
        const { networks } = prevProps
        if(networks != this.props.networks) this.setNetworks()
    }

    render(){
        const {loader, networkList} = this.state
        const { onBoarding, setNetwork, navigate } = this.props
        const isShopify = shopifyApp();
        const pageContent = window?.APP_CONTENT?.manual_upload ?? {};
        return (
            <>
                <div className="custom-side-menu d-flex">
                {!isShopify ? <SideMenu {...{getWallData:{id: localStorage.getItem('wallId')}}}/> : null}
                <div className="custom-main-content"  style={{ width: isShopify ? "100%" : null, marginLeft: isShopify ? "0" : null }}>
                <div className='d-flex flex-column flex-column-fluid w-100 py-5 manual_upload_'>
                    {/* {!onBoarding ? <div className="subheader">
                        <Container fluid>
                            <div className='d-flex align-items-center'>
                                <Link to={`/content/addfeed/${localStorage.getItem('wallId')}`} className="btn btn-icon bg-gray-100 btn-sm me-2" title='Upload Feed'><FontAwesomeIcon icon={'angle-left'} iconClass={'me-0'}/></Link>
                                <h1 className="me-2 h4 my-3">Upload Feed</h1>
                            </div>
                        </Container>
                    </div> : null} */}
                    <Container>
                        <Card className={`${onBoarding?'shadow-none border-0':''}`}>
                            <Card.Body className='f-center flex-column manual_upload_card'>
                                <h3 className='text-center mb-3'>{pageContent?.page_title ?? ""}</h3>
                                <p className='text-center mb-8'>{pageContent?.page_desc ?? ""}</p>
                                <div className='f-center flex-wrap w-100 flex-column'>
                                    {!loader ? 
                                    <Row className='w-100' style={{maxWidth:600}}>
                                        {/* <Col>
                                            <Button variant='outline-secondary' onClick={() => onBoarding ? setNetwork(0) : navigate(`/manual-upload/feed?network=0`)} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                                <p className='display-2 mb-3'>
                                                    <FontAwesomeIcon type={'light'} icon={'laptop'} iconClass={'me-0'}/>
                                                </p>
                                                <p className='mb-0 fs-10 fw-medium'>My Device</p>
                                            </Button>
                                        </Col> */}

                                        {networkList.length > 0 ? networkList.map(network =><Col>
                                            <Button variant='outline-secondary' onClick={() => onBoarding ? setNetwork(network.networkId) : navigate(`/manual-upload/feed?network=${network.networkId}`)} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                                <p className='display-2 mb-3'>
                                                    {network.networkId == 0 ? <FontAwesomeIcon type={'light'} icon={'laptop'} iconClass={'me-0'}/> : 
                                                    <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.networkId)}.svg`)} height={36} width={36} fluid/>}
                                                </p>
                                                <p className='mb-0 fs-10 fw-medium'>{network.networkId == 0 ? (pageContent?.option_my_device ?? "") : GET_NETWORK_NAME(network.networkId)}</p>
                                            </Button>
                                        </Col>) : null}
                                        <div className="d-flex justify-content-center mt-4">
                                        <Link to={`/content/addfeed/${localStorage.getItem('wallId')}`} className="text-decoration-underline text-muted p-2" title='Upload Feed'>
                                        <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-2'}/>
                                        {pageContent?.option_back ?? ""}</Link>
                                        </div>
                                    </Row> 
                                    :
                                    <Row className='w-100' style={{maxWidth:600}}>
                                    {generateRandomArray(3).map((_, index) => <Col>
                                        <div key={index} className="btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4 d-flex">
                                            <div className="sk_rect h-40px w-40px mb-3"></div>
                                            <div className="sk_line h-10px mb-1 w-75"></div>
                                        </div>
                                        </Col>)}
                                    </Row>
                                    }
                                    {onBoarding ? 
                                        <div className="f-center">
                                            <Button variant='link' className="text-decoration-underline" onClick={() => this.props.onBack('c_n')}>
                                                {pageContent?.btn_choose_network ?? ""}
                                            </Button>
                                        </div>: null}
                                </div>
                            </Card.Body>
                        </Card>
                    </Container>
                </div>
                </div>
                </div>
            </>
        )
        
    }
}

const mapStateToProps = (state) => {
    return {
        networks: state.addFeedReducer?.addFeedNetworks?.networks,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getNetworks: (callback) => dispatch(GET_ADD_FEED(callback)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ManualHead))